raul-text {
  display: block;
  font-size: 0.875rem;
}

raul-text .r-text {
  border-width: 0;
  margin: 0;
}

raul-text .r-text--small {
  font-size: 0.75rem;
  color: rgba(55, 71, 79, 0.8);
  line-height: 18px !important;
}

raul-text .r-text--medium {
  font-size: 0.875rem;
  color: rgba(55, 71, 79, 0.8);
  line-height: 20px !important;
}

raul-text .r-text--large {
  font-size: 1rem;
  font-weight: 700;
  line-height: 20px !important;
}

raul-text .r-text--extra-large {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 26px !important;
}

raul-text .r-text--hero {
  font-size: 2.25rem;
  font-weight: 700;
  line-height: 40px !important;
}

raul-text .r-text--strong {
  font-weight: 600;
  opacity: 1;
}

raul-text .r-text--primary {
  color: #0076CC;
}

raul-text .r-text--danger {
  color: #D01A1F;
}

raul-text .r-text--success {
  color: #139C3E;
}

raul-text .r-text--white {
  color: #fff;
}

raul-text .r-text--align-left {
  text-align: left;
}

raul-text .r-text--align-center {
  text-align: center;
}

raul-text .r-text--align-right {
  text-align: right;
}

raul-text .r-text--align-justify {
  text-align: justify;
}

raul-text .r-text--emphasis {
  font-style: italic;
}

raul-text .r-text--underline {
  text-decoration: underline;
}

raul-text .r-text--line-through {
  text-decoration: line-through;
}

raul-text .r-text--ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

raul-text .r-text--capitalize {
  text-transform: capitalize;
}

raul-text .r-text--uppercase {
  text-transform: uppercase;
}

raul-text .r-text--paragraph {
  margin-bottom: 0.75rem;
}